import { DOCUMENT, Location } from '@angular/common';
import {
  Component,
  HostBinding,
  Inject,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ScullyRoutesService } from '@scullyio/ng-lib';
import { fromEvent } from 'rxjs';
import { Subject, Observable } from 'rxjs';
import { filter, map, switchMap, takeUntil, tap } from 'rxjs/operators';

@Component({
  selector: 'app-blog-nav',
  templateUrl: './blog-nav.component.html',
  styleUrls: ['./blog-nav.component.scss'],
})
export class BlogNavComponent implements OnInit, OnDestroy {
  onDestroy$ = new Subject();
  nav$: Observable<Element[]>;

  @HostBinding('class') get classes(): string {
    return 'block';
  }

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private route: ActivatedRoute,
    private location: Location,
    public scullyRoutesService: ScullyRoutesService
  ) {}

  ngOnInit(): void {
    this.nav$ = fromEvent(window, 'AngularReady').pipe(
      map(ev => {
        return Array.from(
          this.document.querySelectorAll('.prose h2,.prose h3,.prose h4')
        );
      })
    );

    fromEvent(window, 'AngularReady')
      .pipe(
        switchMap(ev => this.route.fragment),
        switchMap(fragment =>
          this.scullyRoutesService
            .getCurrent()
            .pipe(map(c => [fragment, c.route]))
        ),
        filter(([fragment, route]) => !!fragment && !!route),
        tap(([fragment, route]) => this.scrollTo(route, fragment)),
        takeUntil(this.onDestroy$)
      )
      .subscribe();

    // const isVisible = (element: Element) => {
    //   const subject = new Subject<Element>();
    //   const observer = new IntersectionObserver(entries => {
    //     entries
    //       .filter(entry => entry.isIntersecting)
    //       .forEach(e => subject.next(element));
    //   });
    //   observer.observe(element);

    //   return subject.pipe(
    //     takeUntil(
    //       this.onDestroy$.pipe(
    //         tap(() => {
    //           observer.disconnect();
    //         })
    //       )
    //     )
    //   );
    // };

    // this.nav$
    //   .pipe(
    //     switchMap(headers => merge(...headers.map(h => isVisible(h)))),
    //     switchMap(el =>
    //       this.scullyRoutesService.getCurrent().pipe(map(c => [el.id, c.route]))
    //     ),
    //     tap(([el, route]) => this.location.replaceState(`${route}#${el}`)),
    //     takeUntil(this.onDestroy$)
    //   )
    //   .subscribe();

    // this.nav$
    //   .pipe(
    //     switchMap(anchors =>
    //       merge(
    //         ...anchors.map(a =>
    //           fromEvent(a, 'click').pipe(
    //             switchMap(ev =>
    //               this.scullyRoutesService
    //                 .getCurrent()
    //                 .pipe(map(c => [a.id, c.route]))
    //             )
    //           )
    //         )
    //       )
    //     ),
    //     tap(([id, route]) => this.scrollTo(route, id)),
    //     takeUntil(this.onDestroy$)
    //   )
    //   .subscribe();
  }

  ngOnDestroy(): void {
    this.onDestroy$.next();
    this.onDestroy$.complete();
  }

  scrollTo(url: string, id: string): void {
    const elementTop = this.document.getElementById(id).offsetTop;
    this.location.replaceState(`${url}#${id}`);
    // this.document.getElementById(id)?.scrollIntoView({ behavior: 'smooth' });
    window.scroll({
      top: elementTop,
      behavior: 'smooth',
    });
  }

  active(url: string, id: string): string {
    return this.location.path(true) === `${url}#${id}` ? 'active' : '';
  }

  scrollToTop(url: string): void {
    this.location.replaceState(`${url}`);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }

  // scrollToRelated(): void {
  //   const stack = Array.from(
  //     this.document.getElementsByTagName('app-card-stack')
  //   )[0];
  //   stack?.scrollIntoView({ behavior: 'smooth' });
  // }

  headerClasses(header: string): string {
    if (header === 'h2') {
      // return 'text-lg font-semibold';
      return 'text-base leading-8';
    } else if (header === 'h3') {
      return 'text-sm ml-2';
    } else if (header === 'h4') {
      return 'text-xs ml-4';
    }
  }
}
